.__JnHV{color: black !important;}


body{ font-size: calc(16px + (26 - 14) * ((100vw - 200px) / (2560 - 200)));}




@-webkit-keyframes animate {
	from {background-position:0 0;}
	to {background-position: 5000px 0;}
}
 
@-moz-keyframes animate {
	from {background-position:0 0;}
	to {background-position: 5000px 0;}
}
 
@-ms-keyframes animate {
	from {background-position:0 0;}
	to {background-position: 5000px 0;}
}
 
@-o-keyframes animate {
	from {background-position:0 0;}
	to {background-position: 5000px 0;}
}
 
@keyframes animate {
	from {background-position:0 0;}
	to {background-position: 5000px 0;}
} 

.bg{

   
	-webkit-animation: 100s linear 0s normal none infinite animate;
	-moz-animation: 100s linear 0s normal none infinite animate;
	-ms-animation: 100s linear 0s normal none infinite animate;
	-o-animation: 100s linear 0s normal none infinite animate;
	animation: 100s linear 0s normal none infinite animate;
 
}
 
.navlight {
    backdrop-filter: blur(6px) saturate(80%);
    -webkit-backdrop-filter: blur(6px) saturate(80%);
    background-color: rgba(255, 255, 255, 0.5);
    border-radius:0 0 20% 20%;
    border: 1px solid rgba(209, 213, 219, 0.3);
}

.navdark {
    backdrop-filter: blur(6px) saturate(80%);
    -webkit-backdrop-filter: blur(0px) saturate(80%);
    background-color: rgba(17, 25, 40, 0.5);
    border-radius:0 0 20% 20%;
    border: 1px solid rgba(255, 255, 255, 0.125);
}

.sliderholder{  box-shadow: inset 10px 10px 10px rgba(0,0,0,0.05),
    15px 25px 10px rgba(0,0,0,0.1),
    15px 20px 20px rgba(0,0,0,0.05),
    inset -10px -10px 15px rgba(255,255,255,0.9);}