
  
  .btn {
    position: relative;
    width: 140px;
    height: 50px;

  }
  
  .btn a {
    color: inherit;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background: rgba(255, 255, 255, 0.05);
    box-shadow: 0 15px 35px rgba(0, 0, 0, 0.2);
    border-top: 1px solid rgba(255, 255, 255, 0.1);
    border-bottom: 1px solid rgba(255, 255, 255, 0.1);
    border-radius: 30px;
    z-index: 1;
    letter-spacing: 1px;
    text-decoration: none;
    overflow: hidden;
    transition: 0.5s;
    backdrop-filter: blur(15px);
  }
  
  .btn:before,
  .btn:after {
    content: "";
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    bottom: -5px;
    width: 50px;
    height: 10px;
  
    border-radius: 10px;
    transition: 0.5s;
    transition-delay: 0s;
  }
  
  .btn:hover:before,
  .btn:hover:after {
    bottom: 0;
    height: 50%;
    width: 80%;
    border-radius: 30px;
    transition-delay: 0.5s;
  }
  
  .btn:after {
    bottom: initial;
    top: -5px;
  }
  .btn:hover:after {
    bottom: initial;
    top: 0;
  }
  
  .btn:hover a,
  .btn:focus a {
    letter-spacing: 4px;
  }
  
  .btn a:before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 50%;
    height: 100%;
    background: linear-gradient(180deg, rgba(255, 255, 255, 0.15), transparent);
    transform: skewX(45deg) translateX(0);
    transition: 0.5s;
  }
  .btn:hover a:before,
  .btn:focus a:before {
    transform: skewX(45deg) translateX(220%);
  }
  

  